// Custom ticket meta

ul.ticket-meta-primary {
	margin: 0 0 0 0;
	padding-left:1.2em;
	color:darkslategrey;
 }
ul, ol {
	 margin: 0 0 0 0;
}
.entry-meta ul {
	list-style: none;
}
ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

