@media screen and (min-width: 900px) {
	.entry-title {
		font-size: 1em;
		line-height: 96%;
	}
}

.entry-title a {
	text-decoration: none;
	color: black;
}

.entry-title a:focus, .entry-title a:hover {
	border-bottom: 5px solid #b51c35;
}

article.movies, article.live_events, article.attractions, article.sundry {
	flex-direction: column;
	display: flex;
}

.mtx-tiles article.movies, .mtx-tiles article.live_events, .mtx-tiles article.attractions, .mtx-tiles article.sundry {
	display: inline-flex;
	margin-bottom: -10px;
	background-color: #2e2430;
}

.entry-meta,
.entry-footer {
	font-family: "Open Sans", Arial, Helvetica, sans-serif;
	font-size: 90%;
}

.entry-meta a,
.entry-footer a {
	font-weight: 700;
	text-decoration: none;
	color: black;
}

.entry-meta a:focus, .entry-meta a:hover,
.entry-footer a:focus,
.entry-footer a:hover {
	color: black;
	border-bottom: 3px solid #b51c35;
}

.byline::after {
	content: "|";
	margin: 0 .5em;
}

.comments-link::before,
.edit-link::before {
	content: "|";
	margin: 0 .5em;
}

.byline,
.updated:not(.published) {
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.extra {
	display: none;
}

/* Custom Post Meta rules for single post / no sidebar. */
.no-sidebar .entry-meta {
	margin-top: 1.5em;
}

@media screen and (min-width: 900px) {
	.no-sidebar .post-content__wrap .entry-meta {
		margin-top: 2.25em;
		font-size: 80%;
	}
	.no-sidebar .post-content__wrap .entry-meta a {
		font-size: 110%;
		display: block;
		border-bottom: 3px solid white;
	}
	.no-sidebar .post-content__wrap .entry-meta a:hover, .no-sidebar .post-content__wrap .entry-meta a:focus {
		border-bottom-color: #b51c35;
	}
	.no-sidebar .post-content__wrap .byline,
	.no-sidebar .post-content__wrap .posted-on,
	.no-sidebar .post-content__wrap .comments-link,
	.no-sidebar .post-content__wrap .edit-link {
		display: block;
		margin-bottom: 1em;
	}
	.no-sidebar .post-content__wrap .byline:before, .no-sidebar .post-content__wrap .byline:after,
	.no-sidebar .post-content__wrap .posted-on:before,
	.no-sidebar .post-content__wrap .posted-on:after,
	.no-sidebar .post-content__wrap .comments-link:before,
	.no-sidebar .post-content__wrap .comments-link:after,
	.no-sidebar .post-content__wrap .edit-link:before,
	.no-sidebar .post-content__wrap .edit-link:after {
		margin: 0;
		content: "";
	}
	.no-sidebar .post-content__wrap .extra {
		display: inline;
	}
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.entry-footer {
	margin: 1.5em 0;
	font-family: "Open Sans", Arial, Helvetica, sans-serif;
}

section.mtx-tiles {
	width: 100%;
	display: -ms-grid;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(23%, 1fr));
	-webkit-box-align: start;
	-ms-flex-align: start;
	align-items: flex-start;
	grid-gap: 4px;
	outline: 2px solid #00ff15;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
footer {
	background-color: #fff;
}

footer nav {
	display: flex;
	min-height: 65px;
	align-items: center;
	position: relative;
	text-transform: capitalize;
	font-family: "Open Sans", Arial, Helvetica, sans-serif;
	color: #fff;
	max-width: 100%;
	margin: 0 auto;
}

.footer-navigation ul {
	margin: 0;
	padding: 0;
}

.footer-navigation ul li {
	display: inline-flex;
	flex-direction: row;
	flex-grow: 1;
	align-content: center;
	margin-right: 1.5em;
}

.footer-navigation ul li a {
	color: white;
	text-decoration: none;
}

.footer-navigation ul li a:hover, .footer-navigation ul li a:focus {
	text-decoration: underline;
}

#social-menu svg {
	margin: 0 auto;
	color: #f5ecec;
}

#social-menu .svg-inline--fa {
	display: block;
	align-items: center;
	font-size: 2em;
	margin-top: .5em;
}

@media screen and (min-width: 600px) {
	.menu-toggle {
		display: none;
	}
	footer nav {
		max-width: 44%;
	}
	.footer-navigation ul {
		display: block;
		display: flex;
		flex-wrap: wrap;
		list-style: none;
	}
}

.menu-social-links-menu-container {
	align-self: flex-end;
	width: 100%;
}

@media screen and (min-width: 900px) {
	.footer-navigation ul {
		justify-content: center;
		padding-top: 0;
		list-style: none;
	}
}

.site-info, .site-info a {
	font-size: small;
	color: grey;
}

.site-info {
	margin-right: 20px;
	margin-top: 1.2em;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
	margin: 0 0 1.5em;
	/* Make sure select elements fit in widgets. */
}

.widget select {
	max-width: 100%;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
}

/*--------------------------------------------------------------
## Figures > image
--------------------------------------------------------------*/
.featured-cropped {
	background-color: #2e2430;
	background-repeat: no-repeat;
	max-height: 125px;
	min-height: 125px;
	overflow-y: hidden;
}

.featured-cropped img {
	mask: url(assets/images/svg/ticket.svg#ticket-shape) 50% 0%/contain no-repeat;
	min-height: 125px;
}
